<template>
  <a class="m-tabbar-item" :class="{'is-active':isActive}" @click="goToRouter">
    <span class="m-tabbar-item-icon" v-show="!isActive"><slot name="icon-normal"></slot></span>
    <span class="m-tabbar-item-icon" v-show="isActive"><slot name="icon-active"></slot></span>
    <span class="m-tabbar-item-text"><slot></slot></span>
  </a>
</template>



<!-- 
 * @desc  tabbar
 * @param id - 
 * @param isActive - valueid
 * @param $parent.$emit('input',id) - 
 * @param {slot} [icon-normal] - icon-
 * @param {slot} [icon-active] - icon-
 * @param {slot} - 
 * @example
 * <m-tabbar-item id='tab1'> id
 *       <img src="../assets/images/ic_tab_home_normal.png" alt="" slot="icon-normal">
 *       <img src="../assets/images/ic_tab_home_active.png" alt="" slot="icon-active">
 *       
 * </m-tabbar-item>
-->

<script type="text/javascript">
  export default{
    props: {
      id: {
        type: String
      },
      isRouter: {
        type: Boolean,
        default: false
      }
    },
    computed: {
        isActive() {
            if(this.$parent.value===this.id) {
                return true;
            }
        }
    },
    methods: {
        goToRouter() {
            this.$parent.$emit('input',this.id)
          //,
            if(this.isRouter) {

                this.$router.push(this.id)
            // <router-link>  <router-link :to="...">  router.push(...)
              //
              //<router-link :to="...">
                //router.push(...)
            }
        }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">

.m-tabbar-item
  flex: 1
  text-align: center
  .m-tabbar-item-icon
    display: block
    padding-top: 2px
    img
      width: 28px
      height: 28px
  .m-tabbar-item-text
    display: block
    font-size: 10px
    color: #949494
  &.is-active
    .m-tabbar-item-text
      color: #42bd56
</style>



